.Saving_MyBook {
  width: 325px;
  height: 330px;
  background: white;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  perspective: 1000px;
  margin-top: 116px;
}

.Saving_Cover {
  width: 300px;
  height: 100%;
  position: absolute;
  left: 25px;
  background: white;
  transform: rotateY(0deg);
  transform-style: preserve-3d;
  transform-origin: left;
  transition: all 0.5s ease-in;
}

.Saving_MyBook:hover .Saving_Cover{
  transform: rotateY(-180deg);
}

figure {
  margin: 0;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

figure.Saving_front{
  background-image: url("./images/SavingCoverImage.jpg");
  background-repeat: round;
}

figure.Saving_back{
  background: #3d4752;
  transform: rotateY(180deg);
}